* { 
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family:  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(35, 38, 44);
  color: #fff;
} 

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.text-center { 
  text-align: center;
  color: #2ebaee;
}

.container { 
  width: 60%;
  margin: 10% auto;
}


.ListItems { 
  background: #fff;
  color: #2ebaee;
}

.ListItems  p { 
  margin: 0;
  text-align: center;
  padding: 10px 0px;
}

.ListItems span { 
   display: inline-block;
   text-align: center;
   padding: 10px 0;
   font-size: 20px;
   border: 0.3px solid #ccc;
}

.ListItems span.title { 
  background: rgb(35, 38, 44);
}

.ListItems span.name,
form input[type=text]{ 
  width: 54%;
}


.ListItems span.age,
form input[type=number]{ 
  width: 30%;
}

.ListItems span.action ,
form input[type=submit]{ 
  width: 16%;
  
}

.ListItems span.icon{ 
  color: #d89399;
  cursor: pointer;
}

form input { 
  padding: 10px;
  background: #ebebeb;
  border: transparent;
  border: 0.3px solid #ccc;
}

form input[type=submit] { 
  background: #2ebaee;
  border: 0.3px solid #2ebaee;
  color: #fff;
  cursor: pointer;
}





 
